/* eslint-disable import/no-named-as-default */
import {Route, Switch} from "react-router-dom";

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as actions from '../actions/userLoginActions';

import AuthenticatedRoute from './AuthenticatedRoute/AuthenticatedRoute';
import AboutPage from "./AboutPage";
import FuelSavingsPage from "./containers/FuelSavingsPage";
import LoginPage from "./LoginPage/LoginPage";
import LandingPage from "./LandingPage/LandingPage";
import NotFoundPage from "./NotFoundPage";
import PropTypes from "prop-types";
import React from "react";
import {hot} from "react-hot-loader";
import {parse} from 'query-string';

// This is a class-based component because the current
// version of hot reloading won't hot reload a stateless
// component at the top-level.

class App extends React.Component {

  constructor(props) {
    super(props);
    this.redirectToLogin = this.redirectToLogin.bind(this);
    var token = window.location.search.replace("?token=","");
    if (this.props.location && this.props.location.search) {
      const query = parse(this.props.location.search);
      if (query && query.token) {
        token = query.token;
      }
    }
    this.props.actions.checkToken(token);
  }


  redirectToLogin() {
    window.location = window.location.origin;
  }


  render() {

    return (
      <div>
        <Switch>
          <Route exact path="/" component={LoginPage}/>
          <AuthenticatedRoute path="/landing-page" component={LandingPage} isLoggedIn={this.props.isUserLoggedIn}
                              redirectToLogin={this.redirectToLogin}/>
          <AuthenticatedRoute path="/fuel-savings" component={FuelSavingsPage} isLoggedIn={this.props.isUserLoggedIn}
                              redirectToLogin={this.redirectToLogin}/>
          <AuthenticatedRoute path="/about" component={AboutPage} isLoggedIn={this.props.isUserLoggedIn}
                              redirectToLogin={this.redirectToLogin}/>
          <Route component={NotFoundPage}/>
        </Switch>
      </div>
    );
  }
}

App.propTypes = {
  children: PropTypes.element,
  actions: PropTypes.object,
  location: PropTypes.object,
  isUserLoggedIn: PropTypes.bool
};

// export default hot(module)(App);

function mapStateToProps(state) {
  return {
    isUserLoggedIn: state.isUserLoggedIn
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}

export default hot(module)(connect(
  mapStateToProps,
  mapDispatchToProps
)(App));
